.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
input{
  height: 35px;
  margin: 10px;

  border-radius: 20px;
}
.btn{
  width: 120px;
  background-color: aqua;
}
.formContain{
  
  display: flex;
  border: 2px solid black;
  width: 950px;
  margin-left: 17%;
  margin-top: 20px;
  justify-content: center;
  flex-wrap: wrap;
}
.box{
  width: 45%;
  height: 40px;
  background-color: rgb(206, 130, 130);
  border: 1px solid black;
  margin: 20px;
  padding-top: 5px ;
  border-radius: 10px;
}